}

.gitContainer {
  padding-right: 15px;
  padding-left: 15px;
  display: flex;
}

.gitButton {
  position: relative;
  display: block;
  margin: 30px auto 0;
  padding: 14px 45px;
  color: #fff;
  font-size: 14px;
  border: 2px solid #D24D57;
  border-radius: 0;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  letter-spacing: .08em;
  text-shadow: 0 0 1px rgb(0 0 0 / 20%), 0 1px 0 rgb(0 0 0 / 20%);
  transition: all 1s ease;
}

.gitButton::after{
  background: #d24d57;
  opacity: .5;
  -webkit-transform: translateY(-50%) translateX(-50%) rotate(90deg);
          transform: translateY(-50%) translateX(-50%) rotate(90deg);
  content: "";
  position: absolute;
  height: 0%;
  left: 50%;
  top: 50%;
  width: 150%;
  z-index: -1;
  transition: all .75s ease 0s;
}

.gitButton:hover::after {
  opacity: 1;
	height: 600% !important;
}

.btn {
  box-sizing: border-box;
  -webkit-appearance: none;
          appearance: none;
  background-color: transparent;
  border: 2px solid #D24D57;
  border-radius: 0.6em;
  color: white;
  cursor: pointer;
  display: flex;
  align-self: center;
