  font-family: "Bebas Neue";
  font-weight: 300;
  font-size: 1.8rem;
}

.btn:hover, .btn:focus {
  color: white;
  outline: 0;
}

.submitButton {
  border-color: #D24D57;
  border-radius: 0;
  color: white;
  position: relative;
  overflow: hidden;
  z-index: 1;
  text-shadow: 0 0 1px rgb(0 0 0 / 20%), 0 1px 0 rgb(0 0 0 / 20%);
  transition: all 1s ease;
}

.submitButton::after {
  content: '';
  position: absolute;
  display: block;
  opacity: .5;
  -webkit-transform: translateY(-50%) translateX(-50%) rotate(90deg);
          transform: translateY(-50%) translateX(-50%) rotate(90deg);
  height: 0%;
  left: 50%;
  top: 50%;
  width: 150%;
  background: #D24D57;
  z-index: -1;
  transition: all .75s ease 0s;
}

.submitButton:hover {
  color: white;
}

.submitButton:hover::after {
  width: 110%;
  opacity: 1;
	height: 600% !important;
}


