.Button_Wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  align-items: center;
  margin-top: 50px;
}
.ButtonAnimation_Item {
  margin-top: 1em;
}
.ButtonAnimation {
  position: relative;
  text-decoration: none;
  /* display: inline-block; */
  padding: 0.75vmax 28px;
  line-height: normal;
  border: 1px solid transparent;
  border-radius: 0;
  text-transform: uppercase;
  font-size: 0.75vmax;
  text-align: center;
  letter-spacing: 0.2em;
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  font-style: normal;
  background-color: transparent;
}
.ButtonAnimation::before {
  position: absolute;
  content: "";
  display: block;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  -webkit-transform-origin: left center;
          transform-origin: left center;
  z-index: -1;
}
.ButtonAnimation:not([disabled]):hover::before {
  -webkit-transform-origin: right center;
          transform-origin: right center;
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
}
.ButtonAnimation:not([disabled])::before {
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  transition-duration: 0.45s;
  transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition-delay: 0s;
}
#section-header .ButtonAnimation {
  color: #363636;
  border-color: #ffffff;
}
#section-header .ButtonAnimation::before {
  background-color: #ffffff;
}
@media (-moz-touch-enabled: 0), (hover: hover) {
  #section-header .ButtonAnimation:hover {
    color: #ffffff;
  }
}

